@import url(https://fonts.googleapis.com/css?family=Open+Sans+Condensed:300|PT+Sans+Narrow&display=swap);
*{
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
  font-family: 'Raleway', sans-serif;
  word-wrap: break-word;
}

.hide{
  display: none;
}

.show {
  display: default;
}
.website{display:flex;flex-direction:column;height:100vh;width:100vw}

.nav{background-color:#fff9ef;width:100%;height:65px;border-bottom:1px solid black;position:relative;padding:0px 25px;display:flex;flex-direction:row;justify-content:space-between;align-items:center}

.burger{cursor:pointer;display:flex;flex-direction:column;justify-content:center;margin-right:30px}.burger .burger-line{height:1px;width:30px;background-color:black;margin:5px}.burger:hover .burger-line{height:3px;border-radius:100%;background-color:#464646}.burger:active .burger-line{background-color:#9e9e9e}

.nav-items{height:70px;width:400px;display:flex;flex-direction:row;justify-content:space-between}.nav-items .nav-link{display:flex;align-items:center;height:70px;color:#707070;text-decoration:none}.nav-items .nav-link:hover{color:black}

.nav-logo{display:flex;align-items:center;height:70px;color:black;text-decoration:none;-webkit-text-emphasis:filled #000;text-emphasis:filled #000;font-weight:bolder;font-size:1.3rem}

.drop-nav{z-index:-1;width:100%;position:absolute;border-bottom:1px solid black;height:70px;background-color:rgba(255,255,255,0.9);display:flex;flex-direction:row;justify-content:space-evenly;align-items:flex-end;padding-bottom:5px;top:0}.drop-nav .nav-link{text-decoration:none;color:#505050}.drop-nav .nav-link:hover{color:black}

#navHeighAnchor{position:relative;widows:100px;z-index:4}

.home-page{-webkit-animation:colorchange 50s infinite;animation:colorchange 50s infinite;width:100%;position:relative;height:93vh}@-webkit-keyframes colorchange{0%{background:#ff8686}9%{background:#dc3b3b}18%{background:#dc3bc1}27%{background:#973bdc}36%{background:#453bdc}45%{background:#3ba8dc}54%{background:#3bdcbe}63%{background:#3bdc6d}72%{background:#4cdc3b}81%{background:#a6dc3b}90%{background:#dcb93b}100%{background:#ff8686}}@keyframes colorchange{0%{background:#ff8686}9%{background:#dc3b3b}18%{background:#dc3bc1}27%{background:#973bdc}36%{background:#453bdc}45%{background:#3ba8dc}54%{background:#3bdcbe}63%{background:#3bdc6d}72%{background:#4cdc3b}81%{background:#a6dc3b}90%{background:#dcb93b}100%{background:#ff8686}}


:root{
  --pixel-size: 0.6;
  --move-time:1;
}

.character{
  position: relative;
  width: calc(4.79rem * 0.6);
  width: calc(4.79rem * var(--pixel-size));
  height: calc(6.4rem * 0.6);
  height: calc(6.4rem * var(--pixel-size));
  overflow: hidden;
}

.character-sprite-sheet{
  position: absolute;
  width: calc(20rem * 0.6);
  width: calc(20rem * var(--pixel-size));
  -webkit-animation:  moveSpriteSheet steps(4) calc(0.5s * 1)   infinite;
          animation:  moveSpriteSheet steps(4) calc(0.5s * 1)   infinite;
  -webkit-animation:  moveSpriteSheet steps(4) calc(0.5s * var(--move-time) )   infinite;
          animation:  moveSpriteSheet steps(4) calc(0.5s * var(--move-time) )   infinite;
}

.shadow{
  position: absolute;
  width: calc(4rem * 0.6);
  width: calc(4rem * var(--pixel-size));
  height: calc(1rem * 0.6);
  height: calc(1rem * var(--pixel-size));
  top: calc(5.6rem * 0.6);
  top: calc(5.6rem * var(--pixel-size));
  left: calc(0.5rem * 0.6);
  left: calc(0.5rem * var(--pixel-size));
}

.pixelArt {
  -ms-interpolation-mode: nearest-neighbor;
      image-rendering: -moz-crisp-edges;
      image-rendering: pixelated;
}

@-webkit-keyframes moveSpriteSheet {
  from {
    -webkit-transform: translate3d(0%, 0, 0);
            transform: translate3d(0%, 0, 0);
  }
  to {
    -webkit-transform: translate3d(-100%, 0, 0);
            transform: translate3d(-100%, 0, 0);
  }
}

@keyframes moveSpriteSheet {
  from {
    -webkit-transform: translate3d(0%, 0, 0);
            transform: translate3d(0%, 0, 0);
  }
  to {
    -webkit-transform: translate3d(-100%, 0, 0);
            transform: translate3d(-100%, 0, 0);
  }
}

.stand{
  -webkit-animation: none;
          animation: none;
  -webkit-transform: translate3d(-3rem, 0px, 0);
          transform: translate3d(-3rem, 0px, 0);
}

.right{
  top: -3.96rem;
  left: 0.3rem;
}

.left{
  top: -8rem;
  left: 0.1rem;
}

.up{
  left: -0.04rem;
  top: -12rem;
}
.x-container{
  position: relative;
  overflow-x: hidden;
  height: 100%;
}

.svg-line{
  position: absolute;
}
